.new {
    width: 100%;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    padding-top: 7%;
    background: linear-gradient(90deg, #5041b2 0%, #7969e6 100%); }

.title {
    text-align: center; }

.wrapper {
    width: 90%;
    max-width: 500px; }

.form {
    &>div {
        &:first-child {
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                background: #fff;
                opacity: .5;
                margin: 20px 0; } } }
    &>hr {
        border: 0;
        height: 1px;
        background: #fff;
        opacity: .5;
        margin-bottom: 15px; } }
